<template>
	<div id="miracle">
		<div id="miraclebg">
			<div id="miraclePanel">
				<div id="miracleLeft"></div>
				<div id="miracleItem">
					<div id="miracleItemRight"></div>
					<div id="miracleItemLeft"></div>
				</div>
				<div id="miracleRight"></div>
			</div>
		</div>
	</div>
</template>

<script>
import {mapState} from 'vuex';
export default{
	name : 'miracle',
